import React, { cloneElement } from 'react';
import { Route, useHistory, useLocation } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { userSelector } from '../../../../utils/redux/user';
import AffiliateLanding from '../../../pages/affiliates/Landing';
import AffiliateLogin from '../../../pages/affiliates/Login';
import AffiliateSignup from '../../../pages/affiliates/Signup';
import AffiliateDashboard from '../../../pages/affiliates/Dashboard';
import AffiliateDisclaimer from '../../../pages/affiliates/Disclaimer';

export default function AffiliateRoutes() {
  return <>
    <Route exact path="/login"><AffiliateLogin /></Route>
    <Route exact path="/signup"><AffiliateSignup /></Route>
    <Route exact path="/"><RouteWrapper mustBeLoggedIn><AffiliateDashboard /></RouteWrapper></Route>
    <Route exact path="/disclaimer"><AffiliateDisclaimer /></Route>
    <Route exact path="/overview"><AffiliateLanding /></Route>
  </>
};


function RouteWrapper({children, mustBeLoggedIn, ...props}: {children: JSX.Element, mustBeLoggedIn?: Boolean}){
  const history = useHistory();
  const user = useSelector(userSelector);
  if(mustBeLoggedIn){
    if(!user){
      history.push('/')
      return null;
    }
  }  
  return <>    
    {
      cloneElement(children, props)
    }
  </>
}